<template>
    <div id="theme">
        <b-nav-item-dropdown text="" right>
            <b-dropdown-item href="#" @click="dayOrNight(1)"></b-dropdown-item>
            <b-dropdown-item href="#" @click="dayOrNight(0)"></b-dropdown-item>
            <b-dropdown-item href="#" @click="dayOrNight(2,0)">-</b-dropdown-item>
            <!--  -->
            <b-modal v-model="diyColor" title="" :header-class="'body-class'" dialog-class="dialog-class"
            :body-class="'body-class'" modal-class="modal-class" hide-footer>
            <form ref="form" @submit.stop.prevent="handleSubmit" class="dilog-input">
                <b-card>
                    <!-- <ck-color-picker v-model="color"  @change="changeColor($event)"/> -->
                    <colorPicker :value="color" @input="updateValue"></colorPicker>
                </b-card>
                <b-button class="mt-3" variant="outline-primary" block @click="diyColor=false"> </b-button>
            </form>
            </b-modal>
            <b-dropdown-item href="#" @click="dayOrNight(3,0)">-</b-dropdown-item>
            <b-modal v-model="diyBg" title="" :header-class="'body-class'" dialog-class="dialog-class"
            :body-class="'body-class'" modal-class="modal-class" hide-footer>
            <form ref="form" @submit.stop.prevent="handleSubmit" class="dilog-input">
                <b-form-file multiple :file-name-formatter="formatNames" :placeholder="tip" browse-text="" autofocus @change="choosePic" accept="image/*"></b-form-file>
                <b-button class="mt-3" variant="outline-primary" block @click="diyBg=false"> </b-button>
            </form>
            </b-modal>
        </b-nav-item-dropdown>
    </div>
</template>

<script>
// 
import theme from '@/assets/js/theme'
// import colorPicker from '@/assets/js/colorPicker'
import {Sketch  as colorPicker} from 'vue-color'
let themeEle = document.getElementById('style')
let mainArea=document.getElementsByClassName('content-theme')[0]
    export default {
        data(){
            return{
                diyColor:false, //
                diyBg:false,     //
                tip:'',
                color: '#0D96C3A8', //
                colorShow:true
            }
        },
        methods: {
            //  -  falsetrue
            dayOrNight(flag,judeg) {
                flag=Number(flag)
                switch (flag) {
                    case 0: { // 
                        themeEle.innerHTML = theme.day
                    };
                    break;
                    case 1: { // 
                        themeEle.innerHTML = null
                    };
                    break;
                    case 2:{ // 
                        if(judeg && this.$store.state.diyColor && this.$store.state.theme==flag){
                            this.changeColor(this.$store.state.diyColor)
                        }else{
                            if(!this.$store.state.diyColor){}
                            this.diyColor=true
                            this.changeColor()
                        }
                    };break;
                    case 3:{ // 
                        if(judeg && this.$store.state.diyBg && this.$store.state.theme==flag){
                            this.choosePic()
                        }else{
                            if(!this.$store.state.diyBg) this.tip=''
                            this.diyBg=true
                            this.choosePic()
                            this.$store.commit('alert','')
                        }
                        
                    };break;
                }
                this.$store.commit('changeTheme', flag)
            },
            // 
            formatNames(files) {
                if (files.length === 1) {
                    return files[0].name
                } else {
                    return `${files.length} `
                }
            },
            // 
            choosePic(files){
                let url
                let oldSty=themeEle.innerHTML.toString().split('.content-theme')[0] //
                if(files){
                    url=this.getImgUrl(files.target.files[0])
                }else{
                    url=this.$store.state.diyBg
                }
                let sty=theme.diyBg(url)
                themeEle.innerHTML=`${oldSty.trim()}${sty}`
                this.$store.commit('diyBg',url)
            },
            // 
            getImgUrl(file) {
                var url = null ;
                //  js 
                if (window.createObjectURL!=undefined) { // basic
                    url = window.createObjectURL(file) ;
                } else if (window.URL!=undefined) { // firefox
                    url = window.URL.createObjectURL(file) ;
                } else if (window.webkitURL!=undefined) { // webkitchrome
                    url = window.webkitURL.createObjectURL(file) ;
                }
                return url ;
            },
            // 
            updateValue (val) {
                this.color = val.hex8
                
                this.changeColor(this.color)
            },
            // 
            changeColor(color){
                let oldSty=themeEle.innerHTML.toString().split('.content-theme')[0] //
                if(color){}
                else{color=this.color}
                let sty=theme.diyColor(color)
                themeEle.innerHTML=`${oldSty.trim()}${sty}`
                this.$store.commit('diyColor',color)
            }
        },
        mounted() {
            // 
            this.dayOrNight(this.$store.state.theme,1)
        },
        watch:{
            // 
            '$store.state.theme':{
                handler(val){
                    this.dayOrNight(this.$store.state.theme,1)
                },
            }
        },
        components:{
            colorPicker
        }
    }
</script>

<style lang="less" scoped>

</style>